  scroll-behavior: smooth;
}

/* width */

::-webkit-scrollbar {
  width: 2px;
}

/* Track */

::-webkit-scrollbar-track {
  background: #9f9f9f;
}

/* Handle */

::-webkit-scrollbar-thumb {
  background: #555;
}

/* Handle on hover */

::-webkit-scrollbar-thumb:hover {
  background: #1de8ff;
}

